import { g } from './common'
/**
 * Chapter4 砼重力（拱）坝坝身泄水建筑物水力学计算
 * 部分计算参考以下网址
 * https://raymondmcguire.github.io/?e=HYCAL&&theme=Image-Visual-Lab
 */
export module Chapter4 {

    /**
     * 4.1开敞式溢流堰的泄量
     * @param C
     * @param m
     * @param eplison
     * @param sigma_m
     * @param n
     * @param B
     * @param H_w
     * @returns Q
     */
    export function kcsylydxl(C: number, m: number, eplison: number, sigma_m: number, n: number, B: number, H_w: number) {
      let Q = C * m * eplison * sigma_m * n * B * Math.sqrt(2 * g) * Math.pow(H_w, 3 / 2)
      // console.log(Q);
      return Q
    }

    /**
     * 4.2孔口泄流计算
     * @param A_i
     * @param ksi_i
     * @param A_j
     * @param X_j
     * @param l_j
     * @param n_z
     * @param A_k
     * @param n
     * @param H_w
     * @returns Q
     */
    export function kkxljs(A_i: Array<number>, ksi_i: Array<number>, A_j: Array<number>, X_j: Array<number>, l_j: Array<number>, n_z: number, A_k: number, n: number, H_w: number) {
      let R_j = new Array<number>(A_j.length)
      for (let index = 0; index < R_j.length; index++) {
        R_j[index] = A_j[index] / X_j[index]
      }

      let sum_i = 0
      let sum_j = 0
      for (let index = 0; index < A_i.length; index++) {
        sum_i += ksi_i[index] * Math.pow((A_k / A_i[index]), 2)
      }

      for (let index = 0; index < A_j.length; index++) {
        sum_j += (2 * g * n_z * n_z * l_j[index] / Math.pow(R_j[index], 4 / 3)) * Math.pow((A_k / A_j[index]), 2)
      }

      let miu = 1 / (Math.sqrt(1 + sum_i + sum_j))
      // console.log(miu);
      let Q = miu * n * A_k * Math.sqrt(2 * g * H_w)
      let V_k = Q / (n * A_k)
      // console.log(Q);
      // console.log(V_k);
      return [Q, V_k]
    }

    /**
     * 4.3水面波动及掺气计算
     * @param h
     * @param zeta
     * @param v
     * @returns h_b
     *
     */
    export function smbdjsqjs(h: number, zeta: number, v: number) {
      let h_b = (1 + zeta * v / 100) * h
      // console.log(h_b);
      return h_b
    }

    /**
     * 4.4挑流计算
     * @param h
     * @param theta
     * @param H_0
     * @param h_2
     * @param K
     * @param q
     * @param H
     * @param t_d
     * @returns L_total,T
     *
     */
    export function tljs(h: number, theta: number, H_0: number,
      h_2: number, K: number, q: number, H: number, t_d:number) {
      let h_1 = h / Math.cos(theta)
      let v_1 = 0.95 * Math.sqrt(2 * g * (H_0 - h_1))
      let L = v_1 * v_1 * Math.cos(theta) * (Math.sin(theta) + Math.sqrt(Math.sin(theta) * Math.sin(theta) + 2 * g * (h_1 + h_2) / (v_1 * v_1))) / g
      let t = K * Math.pow(q, 0.5) * Math.pow(H, 0.25)

      let tan_beta = Math.sqrt(Math.tan(theta) * Math.tan(theta) + 2 * g * (h_2 + h * Math.cos(theta)) / (v_1 * v_1 * Math.cos(theta) * Math.cos(theta)))
      let L_c = t / tan_beta
      // console.log(L_c);
      let L_total = L + L_c
      let T = t - t_d
      // console.log(T);
      return [L_total, T]
    }

    /**
     * 4.5底流消能水力计算
     * @param E
     * @param Q
     * @param b
     * @param H_0
     * @returns
     * */
    export function dlxnsljs(E:number, Q:number, b:number, H_0:number) {
      let phi = Math.sqrt(1 - 0.1 * Math.pow(E, 1 / 2) / Math.pow((Q / b), 1 / 3))
      // console.log(phi);
      phi = 0.8 // 低坝

      let h_0 = 10
      let learning_rate = 0.01
      let precision = 0.001
      for (let index = 0; index < 10000; index++) {
        let rightValue = (H_0 - Math.pow((Q / b), 2) / (2 * g * phi * phi * h_0 * h_0)) - h_0
        if (Math.abs(rightValue) < precision) {
          // console.log(h_0);
          let v_1 = Q / (b * h_0)
          let F_r1 = v_1 / Math.sqrt(g * h_0)
          let h_2 = h_0 * (Math.sqrt(1 + 8 * F_r1 * F_r1) - 1) / 2
          let L = -1
          if (F_r1 > 4.5) {
            L = 6 * (h_2 - h_0)
          }
          // console.log(v_1);
          // console.log(h_2);
          // console.log(F_r1);
          // console.log(L);
          return [h_0, h_2, F_r1, L]
        }
        h_0 -= learning_rate * rightValue
      }

      return [-1]
    }
}
